$button-focus-bg-color: $DoveGray;
$black-button-bg-color: $Shark;
$bordered-button-border-color: $Shark;
$bordered-button-active-bg-color: $Niagara;
$bordered-button-active-color: $White;
$bordered-button-active-border-color: $Niagara;
$green-bordered-button-border-color: $Niagara;
$green-bordered-button-color: $Niagara;
$white-button-bg-color: $White;
$white-button-color: $Shark;
$green-button-bg-color: $Niagara;
$green-button-color: $White;
$red-button-bg-color: $Monza;
$red-button-color: $White;
$facebook-button-border-color: $Chambray;
$facebook-button-color: $Chambray;
$twitter-button-border-color: $DodgerBlue;
$twitter-button-color: $DodgerBlue;

.btn {
  display: flex;
  justify-content: center;
  align-items: center;

  border: 1px solid transparent;

  font-weight: $regular;
  line-height: 1.5;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;

  cursor: pointer;

  outline: none;
  vertical-align: middle;
  user-select: none;
  transition: background .3s ease;
  border-radius: 5px;

  &:active {
    opacity: .8;
  }
  &:focus {
    outline: 2px dashed rgba($button-focus-bg-color, .5);
  }
  &.-xsmall {
    padding: 7px $sm;

    font-size: $fs-13;
  }
  &.-small {
    padding: $sm $md;

    font-size: $fs-14;
  }
  &.-medium {
    padding: 7px $lg;

    font-size: $fs-16;
  }
  &.-large {
    padding: $lg $xl;

    font-size: $fs-20;
    font-weight: $bold;
  }
  &.-black {
    background-color: $black-button-bg-color;
    color: $White;
    &:active {
      background-color: lighten($black-button-bg-color, 10%);
    }
  }
  &.-border {
    border-color: $bordered-button-border-color;
    &:active {
      background: $bordered-button-active-bg-color;
      color: $bordered-button-active-color;

      border-color: $bordered-button-active-border-color;
    }
  }
  &.-greenBorder {
    color: $green-bordered-button-color;

    border-color: $green-bordered-button-border-color;
  }
  &.-link {
    text-decoration: underline;
  }
  &.-white {
    background-color: $white-button-bg-color;
    color: $white-button-color;
  }
  &.-greenBtn {
    position: relative;

    background: $green-button-bg-color;
    color: $green-button-color;
  }
  &.-red {
    background: $red-button-bg-color;
    color: $red-button-color;
  }
  &.-guestLogin {
    height: 50px;

    font-weight: $semi-bold;
  }
  &.-facebook {
    position: relative;

    height: 50px;

    border: solid 1px $facebook-button-border-color;

    color: $facebook-button-color;

    &:before {
      content: $bo-facebook;

      position: absolute;
      top: calc(50% - 15px);
      left: 5%;

      font-family: "boyner-mobile" !important;
      font-size: $fs-22;
    }
  }
  &.-twitter {
    position: relative;

    height: 50px;

    border: solid 1px $twitter-button-border-color;

    color: $twitter-button-color;

    &:before {
      content: $bo-twitter;

      position: absolute;
      top: calc(50% - 15px);
      left: 5%;

      font-family: "boyner-mobile" !important;
      font-size: $fs-22;
    }
  }
  &.-arrow {
    &:after {
      content: $bo-arrow-right;

      position: absolute;
      top: calc(50% - 16px);
      right: 5%;

      font-family: "boyner-mobile" !important;
    }
  }
  &.-back {
    position: relative;

    padding-left: 25px !important;

    font-weight: $bold;
    &:before {
      content: $bo-arrow-left;

      position: absolute;
      top: calc(50% - 12px);
      left: 0;

      font-family: "boyner-mobile" !important;
      font-size: $fs-16;
    }
  }
  &.-rounded {
    border-radius: 100px;
  }
}
