$product-info-brand-color: $CodGray;
$product-modal-info-bg-color: $Alabaster;

.o-productModalInfo {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;

  padding: $lg;

  background-color: $product-modal-info-bg-color;

  &__image {
    display: flex;
  }

  &__description {
    margin-left: $lg;
  }

  &__brand {
    margin-bottom: $xs;

    color: $product-info-brand-color;

    font-size: $fs-14;
    font-weight: $bold;
    line-height: 20px;
  }

  &__title {
    color: $product-info-brand-color;

    font-size: $fs-12;
    line-height: 16px;
  }
}
