$product-bg-color: $Gallery;
$product-detail-bg-color: $White;
$product-detail-brand-color: $CodGray;
$product-detail-subtitle-color: $CodGray;
$product-detail-name-color: $CodGray;
$discount-badge-bg: $Cinnabar;
$discount-badge-color: $White;
$isFreeCargo-border-color: $Alto;
$isFreeCargo-icon-color: $Niagara;
$product-detail-size-chart-button-color: $CodGray;
$size-chart-border-color: $Alto;
$slider-bg-color: $White;
$slider-header-color: $CodGray;
$slider-brand-color: $CodGray;
$slider-product-name-color: $CodGray;
$slider-old-price-color: $DustyGray;
$slider-price-color: $CodGray;
$slider-color-options-color: $DustyGray;
$slider-clear-content-link-color: $DustyGray;
$product-details-free-cargo-text-color: $CodGray;

.o-slider {
  position: relative;

  padding: 0;
  margin-top: 0;

  background-color: $slider-bg-color;

  &.-visible {
    padding: $lg;
    margin-top: $sm;
  }

  &__header {
    margin-bottom: $lg;

    color: $slider-header-color;

    font-size: $fs-16;
    line-height: 22px;
  }

  &__description {
    display: flex;
    flex-direction: column;
    width: 100%;

    margin-top: $sm;

    text-align: left;
  }

  &__brand {
    margin-bottom: 2px;

    color: $slider-brand-color;

    font-size: $fs-14;
    font-weight: $regular;
    line-height: 18px;
  }

  &__productName {
    overflow: hidden;

    margin-bottom: $sm;

    color: $slider-product-name-color;

    font-size: $fs-12;
    font-weight: $regular;
    line-height: 16px;

    @include ellipsis(100%);
  }

  &__oldPrice {
    margin-bottom: 2px;

    color: $slider-old-price-color;

    font-size: $fs-14;
    line-height: 18px;
    text-decoration: line-through;
  }

  &__price {
    margin-bottom: $sm;

    color: $slider-price-color;

    font-size: $fs-14;
    font-weight: $bold;
    line-height: 20px;
  }

  &__colorOptions {
    color: $slider-color-options-color;

    font-size: $fs-12;
    line-height: 16px;
  }

  &__sliderLink {
    width: 100%;
  }

  &__clearContent {
    position: absolute;
    top: 22px;
    right: 20px;

    color: $slider-clear-content-link-color;

    font-size: $fs-14;
    line-height: 20px;
  }
}

.o-product {
  background-color: $product-bg-color;
  &__detail {
    padding: $lg;

    background-color: $product-detail-bg-color;
  }
  &__brand {
    margin-bottom: $xs;

    font-size: $fs-16;
    font-weight: $bold;
    line-height: 22px;
    &Link {
      color: $product-detail-brand-color;
    }
  }
  &__subtitle {
    font-size: $fs-14;
    line-height: 20px;
    &Link {
      color: $product-detail-subtitle-color;
    }
  }
  &__name {
    padding-bottom: $xl;

    color: $product-detail-name-color;

    font-size: $fs-16;
    font-weight: $regular;
    line-height: 22px;
  }
  &__isFreeCargo {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;

    padding: $lg 0 0 0;
    border-top: .5px solid $isFreeCargo-border-color;

    color: $product-details-free-cargo-text-color;

    font-size: $fs-16;
  }
  &__freeCargoIcon {
    width: 18px;

    margin-top: 2px;
    margin-right: $md;

    fill: $isFreeCargo-icon-color;
  }
  &__freeCargoIconPath {
    fill-rule: evenodd;
  }
  &__sizeChart {
    display: flex;
    justify-content: space-between;

    padding: $lg 0;
    border-top: .5px solid $size-chart-border-color;
  }
  &__sizeChartButton {
    color: $product-detail-size-chart-button-color;

    font-family: $Avenir;
    font-size: $fs-14;
    line-height: 18px;
  }
}

.a-discountBadge {
  position: absolute;
  z-index: z("ground-floor");

  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  min-width: 30px;

  padding: 0 $md;
  margin-top: -50px;

  background-color: $discount-badge-bg;
  color: $discount-badge-color;

  font-size: $fs-14;
  font-weight: $bold;
  line-height: 20px;
}
