$loading-item-bg-color: $Gallery;
$loading-large-item-bg-color: $Gallery;
$loading-large-item-pulse-0-bg-color: rgba($SilverChalice, .1);
$loading-large-item-pulse-50-bg-color: rgba($SilverChalice, .3);
$loading-large-item-pulse-100-bg-color: rgba($SilverChalice, .1);
$btnLoading-bg-color: $White;
$loading-large-bg-color: $White;
$loading-container-bg-color: $White;

.o-loading {
  display: flex;
  flex-direction: row;

  padding: $lg $sm;

  background-color: $loading-container-bg-color;

  &__container {
    width: 100%;
  }

  &__item {
    display: flex;
    flex-direction: column;
    overflow: hidden;

    padding: 0 $sm;
  }

  &__line {
    width: 100%;
    height: 15px;

    margin-bottom: $lg;

    background-color: $loading-item-bg-color;

    animation: pulse 1s infinite ease-in-out;
  }

  &__image {
    width: 100px;
    height: 100px;

    margin-right: $sm;
    margin-bottom: $lg;

    background-color: $loading-item-bg-color;

    animation: pulse 1s infinite ease-in-out;
  }

  &__separator {
    width: 100%;
    height: 2px;

    margin: $xs 0 $xl 0;

    background-color: $loading-item-bg-color;
  }

  &__list {
    display: flex;
    align-items: center;
  }

  & .-right {
    align-items: flex-end;
  }

  & .-inline {
    flex-direction: row;

    .o-loading__line {
      margin-right: $sm;
    }
  }

  & .-h30p {
    height: 30px;
  }

  & .-h45p {
    height: 45px;
  }

  & .-w10p {
    width: 10%;
  }

  & .-w20p {
    width: 20%;
  }

  & .-w30p {
    width: 30%;
  }

  & .-w40p {
    width: 40%;
  }

  & .-w50p {
    width: 50%;
  }

  & .-w60p {
    width: 60%;
  }

  & .-w70p {
    width: 70%;
  }

  & .-w80p {
    width: 80%;
  }

  & .-w90p {
    width: 90%;
  }

  & .-w100p {
    width: 100%;
  }
}

.-wrap {
  flex-flow: wrap;
}

@keyframes pulse {
  0% {
    background-color: $loading-large-item-pulse-0-bg-color;
  }

  50% {
    background-color: $loading-large-item-pulse-50-bg-color;
  }

  100% {
    background-color: $loading-large-item-pulse-100-bg-color;
  }
}
