$modal-header-text-color: $CodGray;
$modal-header-border-color: $Alto;
$modal-header-bg-color: $White;
$modal-content-bg-color: $White;

.o-modal {
  position: fixed;
  z-index: z("roof-top");
  top: 0;
  left: 0;

  display: none;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;

  $modal: &;
  transition: all .3s ease;

  &.-open {
    display: flex;

    #{$modal}__content {
      opacity: 1;
    }
  }

  &__content {
    z-index: z("penthouse");

    width: 100%;
    height: 100%;

    background-color: $modal-content-bg-color;
  }

  &__header {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;

    padding: $lg;
    border-bottom: .5px solid $modal-header-border-color;

    background: $modal-header-bg-color;
    color: $modal-header-text-color;

    font-size: $fs-16;
    font-weight: $bold;
    line-height: 22px;
    text-align: center;
  }

  &__close {
    position: absolute;
    top: 13px;
    left: 25px;

    border: none;
  }

  &__body {
    overflow: auto;
    height: calc(100vh - 106px);

    padding: $md;

    &.-fullScreen {
      height: calc(100vh - 50px);

      padding: 0;

      -webkit-overflow-scrolling: touch;
    }
  }

  &__title {
    margin: $md 0;

    font-size: $fs-20;
  }

  &__description {
    margin: $md 0;

    font-size: $fs-14;
  }
}
