$swiper-bullet-border-color: $White;
$swiper-bullet-active-bg: $CodGray;

.swiper-product-slider {
  position: relative;
}
.swiper-slide {
  display: flex;
}
.swiper-slide-empty {
  align-items: center;
  min-height: 490px;
}
.swiper-slide-img {
  width: 100%;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}
.swiper-container-horizontal {
  > .swiper-pagination-bullets {
    bottom: $xxl;

    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.swiper-pagination-bullet {
  width: 10px;
  height: 10px;

  border: 1px solid $swiper-bullet-border-color;

  background-color: rgba(29,29,29,.5);

  opacity: 1;
}
.swiper-pagination-bullet-active {
  width: 14px;
  height: 14px;

  background-color: $swiper-bullet-active-bg;
}
.swiper-button-next,
.swiper-button-prev {
  width: 36px;
  height: 36px;

  background-image: none;

  &::before {
    content: "";

    position: absolute;
    top: 0;
    left: 0;

    width: 36px;
    height: 36px;

    background-repeat: no-repeat;
    background-position: center;
  }
}
.swiper-button-prev {
  left: $lg;

  &:before {
    background-image: url("/img/arrow_left.svg");
  }
}
.swiper-button-next {
  right: $lg;

  &:before {
    background-image: url("/img/arrow_right.svg");
  }
}
.swiper-zoom-container {
  flex-direction: column;
}

.swiper-lazy {
  width: 100%;
}

.swiper-empty-image {
  width: 60px;
  height: 60px;

  margin: auto;
}
