$xs: .3125rem; //5px
$sm: .625rem; //10px
$md: .9375rem; //15px
$lg: 1.25rem; //20px
$xl: 1.5625rem; //25px
$xxl: 1.875rem; //30px

$spaceamounts: (0, 5, 10, 15, 20, 30, 40);
$sides: (top, bottom, left, right);
@each $space in $spaceamounts {
  @each $side in $sides {
    .m#{str-slice($side, 0, 1)}-#{$space} {
      margin-#{$side}: #{$space}px !important;
    }

    .p#{str-slice($side, 0, 1)}-#{$space} {
      padding-#{$side}: #{$space}px !important;
    }
  }
}


@mixin padding($values...) {
  @each $var in $values {
    padding: #{$var};
  }
}

@mixin margin($values...) {
  @each $var in $values {
    margin: #{$var};
  }
}
