$size-chart-tab-wrap-bg-color: $Gallery;
$size-chart-tab-list-border-color: $Alto;
$size-chart-tab-list-bg-color: $White;
$size-chart-tab-list-active-item-color: $CodGray;
$size-chart-tab-list-item-color: $Emperor;
$size-chart-tab-list-item-active-border-color: $Niagara;
$size-chart-tab-content-wrap-bg-color: $White;
$size-chart-table-list-border-color: $Alto;
$size-chart-table-list-item-icon-color: $Black;
$size-chart-table-list-content-bg-color: $Gallery;
$size-chart-table-list-content-color: $CodGray;
$size-chart-table-list-head-color: $Emperor;

.sizeChart {
  .h4 {
    display: none;
  }
  .tab-wrap {
    background-color: $size-chart-tab-wrap-bg-color;
    .tab-list {
      display: flex;
      justify-content: space-between;

      padding: 0 $lg;
      margin-bottom: $sm;
      border-top: .5px solid $size-chart-tab-list-border-color;
      border-bottom: .5px solid $size-chart-tab-list-border-color;

      background-color: $size-chart-tab-list-bg-color;
      li {
        min-width: 110px;

        padding: $lg;

        color: $size-chart-tab-list-item-color;

        font-size: $fs-14;
        font-weight: $bold;
        line-height: 16px;
        text-align: center;

        text-transform: lowercase;

        &::first-letter {
          text-transform: capitalize;
        }

        &.active {
          border-bottom: 2px solid $size-chart-tab-list-item-active-border-color;

          color: $size-chart-tab-list-active-item-color;
        }
      }
    }

    .tab-content-wrap {
      background-color: $size-chart-tab-content-wrap-bg-color;
      .tab-content {
        display: none;

        &.reveal {
          display: block;
        }

        .table-content {
          padding: $xl;
          margin-bottom: 150px;
        }

        .table-list {
          border-top: .5px solid $size-chart-table-list-border-color;

          .open-acc {
            position: relative;

            display: flex;

            padding: $xl $lg;

            &::after {
              content: "";

              position: absolute;
              bottom: 0;
              left: 20px;

              width: calc(100% - 20px);

              border-bottom: .5px solid $size-chart-table-list-border-color;
            }

            &.active {
              font-weight: $bold;

              &::after {
                display: none;
              }

              .icon-plus {
                top: 23px;

                background-image: url("/img/arrow-up.svg");
              }
            }

            .icon-plus {
              position: absolute;
              top: 23px;
              right: 20px;

              display: flex;
              width: 24px;
              height: 24px;

              background-image: url("/img/arrow-down.svg");
              background-repeat: no-repeat;
              background-position: center;
            }
          }
          .content-acc {
            display: none;
            overflow-x: auto;

            border-bottom: .5px solid $size-chart-table-list-border-color;

            table {
              width: 100%;

              border-collapse: collapse;

              thead {
                tr {
                  th {
                    width: 1%;

                    padding: $sm $lg;

                    color: $size-chart-table-list-head-color;

                    font-size: $fs-14;
                    text-align: center;
                    white-space: nowrap;

                    &:last-child {
                      width: 100%;
                    }
                  }
                }
              }
              tbody {
                tr {
                  height: 38px;
                  &:nth-child(odd) {
                    background: $size-chart-table-list-content-bg-color;
                  }
                  td {
                    width: 1%;

                    color: $size-chart-table-list-content-color;

                    font-size: $fs-14;
                    text-align: center;

                    &:last-child {
                      width: 100%;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
